@font-face {
  font-family: Raleway;
  font-weight: 400;
  src: local(Raleway), url("Raleway-Regular.985ce570.woff") format("woff");
}

@font-face {
  font-family: Raleway;
  font-weight: 500;
  src: local(Raleway Medium), url("Raleway-Medium.b5a9cbe7.woff") format("woff");
}

@font-face {
  font-family: Raleway;
  font-weight: 700;
  src: local(Raleway Bold), url("Raleway-Bold.546f2058.woff") format("woff");
}

@font-face {
  font-family: Open Sans;
  font-weight: 400;
  src: local(Open Sans), url("OpenSans-Regular.9f69615d.woff") format("woff");
}

@font-face {
  font-family: Open Sans;
  font-weight: 600;
  src: local(Open Sans SemiBold), url("OpenSans-SemiBold.6cef3c33.woff") format("woff");
}

@font-face {
  font-family: Open Sans;
  font-weight: 700;
  src: local(Open Sans Bold), url("OpenSans-Bold.90c40126.woff") format("woff");
}

:root {
  --primary-color: #ec7979;
  --secondary-color: #ab4949;
  --primary-text: #000;
  --secondary-text: #fff;
  --gray-text: #86878b;
  --primary-background: #fff;
  --secondary-background: #000;
  --social-media-backgound: #f8efef;
  --dark-color: #333;
  --primary-gray: #f5f5f5;
  --secondary-gray: #e2e2e2;
  --form-color: #bdbdbd;
  --tabs-border: #c6cbcd;
  --half-opacity-black: #00000080;
  --half-opacity-white: #ffffff80;
}

html {
  min-width: 320px;
}

body {
  background-color: var(--primary-background);
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 400;
}

h1 {
  text-transform: uppercase;
  font-family: Raleway, sans-serif;
  font-size: 2.66em;
  font-weight: 500;
}

h2 {
  font-size: 2em;
  font-weight: 400;
}

h3, h4 {
  font-family: Open Sans, sans-serif;
  font-weight: 700;
}

h3 {
  margin-bottom: 42px;
  font-size: 1.66em;
}

h4 {
  margin-bottom: 40px;
  font-size: 1.33em;
}

h5 {
  font-size: 1em;
  font-weight: 400;
}

section {
  padding-top: 50px;
  padding-bottom: 50px;
}

.container {
  max-width: 1170px;
  z-index: 1;
  margin: 0 auto;
  position: relative;
}

.section__title {
  text-align: center;
  text-transform: uppercase;
  margin: 50px 0 40px;
}

.section__title:after {
  content: "";
  width: 170px;
  border-bottom: 1px solid var(--primary-color);
  margin: 55px auto 0;
  display: block;
}

.section__description-text {
  color: var(--gray-text);
  text-align: center;
  margin: 0 0 80px;
}

button, .input_submit {
  cursor: pointer;
  width: 100%;
  max-width: 270px;
  color: var(--secondary-text);
  padding: 0;
  font-family: Open Sans, sans-serif;
  font-size: 1em;
  font-weight: 600;
  line-height: 60px;
  transition-duration: .1s;
}

.banner__form .input_text:focus {
  border-color: var(--primary-color);
}

.button_filled, .input_submit {
  background-color: var(--primary-color);
  border: 0;
  outline: none;
}

.button_filled:hover, .button_filled:focus-visible, .input_submit:hover, .input_submit:focus-visible {
  background-color: var(--secondary-color);
}

.button_filled:active, .input_submit:active {
  background-color: var(--primary-color);
  box-shadow: 0 0 10px var(--secondary-color) inset;
}

.button_outline {
  border: 2px solid var(--primary-background);
  background: none;
  line-height: 56px;
}

.button_outline:hover, .button_outline:focus-visible {
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.button_outline:active {
  color: var(--secondary-text);
  border-color: var(--secondary-color);
  background-color: var(--secondary-color);
}

.banner__form__item, .banner__form__item .input_text {
  width: 100%;
  display: inline-block;
}

.banner__form__item {
  margin-bottom: 20px;
}

.input_text {
  border: 0;
  outline: none;
  padding: 0;
  line-height: 50px;
}

.input_text, textarea {
  font-family: Open Sans, sans-serif;
  font-size: .77em;
}

.input_text::placeholder {
  color: var(--form-color);
}

.header {
  background-color: var(--secondary-background);
  z-index: 3;
  position: sticky;
  top: 0;
}

.header > .container {
  justify-content: flex-end;
  display: flex;
}

.header__menu-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__menu-link {
  color: var(--secondary-text);
  padding: 0 30px;
  font-family: Open Sans, sans-serif;
  font-size: .77em;
  font-weight: 600;
  line-height: 76px;
  text-decoration: none;
  transition-duration: .1s;
  display: block;
}

.header__menu-link:hover, .header__menu-link:focus-visible {
  background: var(--primary-color);
  outline: none;
}

.header__menu-link:active {
  background: var(--secondary-color);
}

.header__logo-block {
  flex: 5;
}

.header__logo-link {
  height: 76px;
  margin-left: -50px;
  line-height: 76px;
  display: inline-block;
}

.header__logo-img {
  vertical-align: middle;
  max-height: 30px;
  width: auto;
  transition-duration: .2s;
}

.header__logo-link:hover .header__logo-img {
  transform: translateX(10px);
}

.header__checkbox {
  display: none;
}

.header__menu-block {
  flex: 7;
  justify-content: flex-end;
  display: flex;
}

.header__menu-toggle {
  width: 55px;
  height: 55px;
  display: none;
  position: relative;
}

.header__menu-toggle__line, .header__menu-toggle__line:before, .header__menu-toggle__line:after {
  width: 24px;
  height: 3px;
  background-color: var(--primary-background);
  transition: all .2s;
  display: inline-block;
  position: absolute;
  top: calc(50% - 1.5px);
  left: calc(50% - 12px);
}

.header__menu-toggle__line:before, .header__menu-toggle__line:after {
  content: "";
}

.header__menu-toggle__line:before {
  margin-top: -8px;
}

.header__menu-toggle__line:after {
  margin-top: 8px;
}

.header__checkbox:checked ~ .header .header__menu-block {
  height: calc(100vh - 55px);
}

.header__checkbox:checked ~ section, .header__checkbox:checked ~ footer {
  transition-delay: .4s;
  display: none;
}

.header__checkbox:checked ~ .header .header__menu-toggle .header__menu-toggle__line {
  background: none;
}

.header__checkbox:checked ~ .header .header__menu-toggle .header__menu-toggle__line:before, .header__checkbox:checked ~ .header .header__menu-toggle .header__menu-toggle__line:after {
  margin: 0;
}

.header__checkbox:checked ~ .header .header__menu-toggle .header__menu-toggle__line:before {
  transform: rotate(-45deg);
}

.header__checkbox:checked ~ .header .header__menu-toggle .header__menu-toggle__line:after {
  transform: rotate(45deg);
}

.banner {
  min-height: calc(100vh - 176px);
  background: linear-gradient(to bottom, var(--half-opacity-black), var(--half-opacity-black)), url("banner.51d2964c.webp");
  color: var(--secondary-text);
  background-position: bottom;
  background-repeat: no-repeat;
  align-items: center;
  display: flex;
  position: relative;
}

.banner > .container {
  column-gap: 30px;
  display: flex;
}

.banner__description {
  flex: 7;
}

.banner__logo-img {
  width: 170px;
}

.banner__title {
  margin: 50px 0 45px;
}

.banner__title:after {
  content: "";
  width: 170px;
  border-top: 1px solid var(--primary-background);
  margin-top: 65px;
  display: block;
}

.banner__text {
  max-width: 40%;
}

.banner__button {
  margin-top: 40px;
}

.banner__form-bg {
  flex: 5;
}

.banner__form {
  background: var(--primary-background);
  color: var(--dark-color);
  border-radius: 10px;
  padding: 35px 50px 0;
  overflow: hidden;
}

.banner__form__title {
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 60px;
  font-family: Open Sans, sans-serif;
  font-weight: 700;
}

.banner__form__title_free {
  color: var(--primary-color);
}

.banner__form .input_text {
  border-bottom: 1px solid var(--form-color);
}

.banner__form__submit {
  width: -webkit-fill-available;
  max-width: initial;
  border-radius: 0 0 10px 10px;
  margin: 75px -50px 0;
  font-size: .83em;
}

.social-media {
  background-color: var(--social-media-backgound);
  padding-top: 0;
  padding-bottom: 0;
}

.social-media > .container {
  column-gap: 30px;
  display: flex;
}

.social-media__description {
  flex: 5;
}

.social-media__title {
  margin: 15px 0 30px;
}

.social-media__text {
  color: var(--gray-text);
}

.social-media__icons {
  flex: 7;
}

.icons__list {
  justify-content: space-between;
  margin: 35px 0 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.icons__link {
  width: 70px;
  text-align: center;
  line-height: 70px;
  display: block;
}

.icons__icon {
  vertical-align: middle;
  max-width: 75%;
  max-height: 75%;
}

.tabs {
  padding-top: 100px;
  padding-bottom: 0;
}

.tabs__radio {
  display: none;
}

.tabs__selectors {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.tabs__selector {
  background-color: var(--primary-background);
  border: 1px solid var(--primary-background);
  text-transform: uppercase;
  width: 200px;
  cursor: pointer;
  border-bottom: 0;
  border-radius: 5px 5px 0 0;
  margin-bottom: -1px;
  padding: 0;
  font-family: Open Sans, sans-serif;
  font-size: .77em;
  line-height: 40px;
  position: relative;
}

#tab1:checked ~ .tabs__selectors .tabs__selector[for="tab1"], #tab2:checked ~ .tabs__selectors .tabs__selector[for="tab2"], #tab3:checked ~ .tabs__selectors .tabs__selector[for="tab3"] {
  border-color: var(--tabs-border);
  z-index: 2;
}

.tabs__tab {
  border: 1px solid var(--tabs-border);
  z-index: 1;
  column-gap: 30px;
  display: none;
  position: relative;
}

#tab1:checked ~ .tabs__content .tabs__tab-1, #tab2:checked ~ .tabs__content .tabs__tab-2, #tab3:checked ~ .tabs__content .tabs__tab-3 {
  display: flex;
}

.tabs__description {
  flex: 7;
}

.tabs__description-block {
  padding: 40px 0 55px 100px;
}

.tabs__title {
  margin: 0 0 65px;
}

.tabs__text {
  margin: 0 0 20px;
}

.tabs__button {
  width: 100%;
  max-width: 270px;
  margin-top: 10px;
}

.tabs__image {
  flex: 5;
}

.tabs__image img {
  width: 100%;
  display: block;
}

.benefits__list {
  text-align: center;
  column-gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.benefits__list-item {
  padding: 30px 0 0;
  transition: all .2s;
}

.benefits__icon-block {
  width: 90px;
  height: 90px;
  border: 1px solid var(--primary-color);
  text-align: center;
  border-radius: 50%;
  margin: 0 auto 74px;
  line-height: 90px;
  transition: all .2s ease-in-out;
}

.benefits__icon {
  vertical-align: middle;
  transition: all .2s ease-in-out;
}

.benefits__list-item:hover .benefits__icon-block {
  transform: scale(1.25);
}

.benefits__list-item:hover .benefits__icon {
  transform: scale(.9);
}

.benefits__list-item__description-text {
  margin-bottom: 10px;
}

.pricing {
  background-color: var(--primary-gray);
}

.pricing__options {
  align-items: flex-start;
  column-gap: 30px;
  display: flex;
}

.pricing__option {
  text-align: center;
  flex: 1;
}

.pricing__option:nth-child(2) {
  margin-top: -20px;
  box-shadow: 0 4px 10px #3333;
}

.pricing__option__title, .pricing__option__price, .pricing__option__description-text {
  color: var(--secondary-text);
  background-color: var(--dark-color);
  border: 1px solid var(--primary-gray);
  margin: 0;
}

.pricing__option__title {
  font-family: Open Sans, sans-serif;
  font-size: 1.33em;
  font-weight: 700;
  line-height: 58px;
}

.pricing__option__price {
  font-family: Raleway, sans-serif;
  font-size: 2em;
  font-weight: 700;
  line-height: 98px;
}

.pricing__option__description-text {
  color: var(--half-opacity-white);
  line-height: 48px;
}

.pricing__option__items-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.pricing__option__item {
  background-color: var(--primary-background);
  color: var(--gray-text);
  border: 1px solid var(--primary-gray);
  line-height: 48px;
}

.reviews__content {
  flex-direction: column-reverse;
  display: flex;
}

.reviews__authors {
  column-gap: 30px;
  display: flex;
}

.reviews__author {
  flex: 1;
}

.reviews__author__message {
  height: 180px;
  background-color: var(--secondary-gray);
  border-radius: 10px;
  align-items: center;
  margin-bottom: 65px;
  padding: 0 20px;
  display: flex;
  position: relative;
}

.reviews__author__message:after {
  content: "";
  width: 0;
  height: 0;
  border-top: 20px solid var(--secondary-gray);
  border-left: 15px solid #0000;
  border-right: 15px solid #0000;
  display: block;
  position: absolute;
  bottom: -20px;
  left: 16%;
}

.reviews__author__message-text {
  color: var(--gray-text);
  text-align: center;
  margin: 0;
}

.reviews__author__information {
  column-gap: 30px;
  display: flex;
}

.reviews__author__name {
  margin: 15px 0 25px;
}

.reviews__author__post {
  margin: 0 0 15px;
}

.reviews__dots {
  justify-content: center;
  column-gap: 50px;
  margin-top: 20px;
  display: none;
}

.reviews__dots__label {
  width: 8px;
  height: 8px;
  border: 1px solid var(--gray-text);
  border-radius: 50%;
  display: inline-block;
}

.reviews__dots__radio {
  display: none;
}

#review_1:checked ~ .reviews__dots .reviews__dots__label[for="review_1"], #review_2:checked ~ .reviews__dots .reviews__dots__label[for="review_2"], #review_3:checked ~ .reviews__dots .reviews__dots__label[for="review_3"] {
  background-color: var(--gray-text);
}

.stylish {
  background: linear-gradient(to bottom, var(--half-opacity-black), var(--half-opacity-black)), url("stylish.251e2aad.webp");
  text-align: center;
  background-position: bottom;
  position: relative;
}

.stylish__title, .stylish__description-text {
  color: var(--secondary-text);
}

.stylish__title:after {
  border-color: var(--primary-background);
}

.stylish__description-text {
  margin-bottom: 75px;
}

.stylish__button {
  max-width: 370px;
}

.contact-us__description-text {
  margin-bottom: 70px;
}

.contact-us__form {
  flex-wrap: wrap;
  column-gap: 30px;
  display: flex;
}

.contact-us__form__required, .contact-us__form__message {
  width: calc(50% - 15px);
}

.contact-us__form__item {
  flex-direction: row-reverse;
  margin-bottom: 40px;
  display: flex;
  position: relative;
}

.contact-us__form__label {
  color: var(--form-color);
  font-size: .77em;
  line-height: 50px;
}

.contact-us__form__item .input_text {
  flex: 1;
  margin-left: 15px;
}

.contact-us__form__item:last-child {
  margin-bottom: 0;
}

.contact-us__form__label[for="message"] {
  display: block;
}

.contact-us__form__textarea {
  width: 100%;
  height: calc(100% - 50px);
  resize: none;
  border: 0;
  outline: none;
  padding: 0;
}

.contact-us__form__label:after {
  content: "";
  width: 100%;
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.contact-us__form__label[for="message"]:after {
  content: none;
}

.contact-us__form__label:after, .contact-us__form__textarea {
  border-bottom: 1px solid var(--form-color);
}

.contact-us__form__item .input_text:focus + .contact-us__form__label:after, .contact-us__form__textarea:focus {
  border-color: var(--primary-color);
}

.contact-us__form__submit {
  max-width: 370px;
  margin: 50px auto 0;
}

.footer {
  background-color: var(--secondary-background);
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer__icons__list {
  justify-content: center;
  column-gap: 30px;
  margin: 0;
  padding: 0;
}

.footer__copyright {
  color: var(--secondary-text);
  opacity: .5;
  text-align: center;
  margin-top: 30px;
}

.footer__icons__link {
  width: 55px;
  height: 55px;
  line-height: 55px;
}

@media (max-width: 1330px) {
  .header__logo-link {
    margin-left: 0;
  }
}

@media (max-width: 1200px) {
  body {
    font-size: 14px;
  }

  header, section, footer {
    padding-left: 15px;
    padding-right: 15px;
  }

  .header__menu-link, .input_text, textarea, .tabs__selector, .contact-us__form__label {
    font-size: 1em;
  }

  .header > .container, .banner > .container, .social-media > .container, .tabs__tab, .benefits__list, .pricing__options, .reviews__author__information, .contact-us__form, .footer__icons__list {
    column-gap: 20px;
  }

  .header > .container:before, .banner > .container:before {
    content: "";
    flex: 1;
  }

  .header__logo-block {
    flex: 4;
  }

  .banner__logo-img {
    width: 115px;
    margin-top: 15px;
  }

  h1, .pricing__option__price {
    font-size: 2.57em;
  }

  .banner > .container:before {
    content: "";
    flex: 1;
  }

  .banner__description {
    flex: 6;
  }

  .banner__title {
    margin-bottom: 30px;
  }

  .banner__title:after {
    width: 115px;
    margin-top: 45px;
  }

  .banner__text {
    max-width: 50%;
  }

  button, .input_submit, .pricing__option__description-text, .pricing__option__item, .footer__copyright {
    font-size: 1.28em;
  }

  .banner__button {
    max-width: 180px;
    margin-top: 12px;
    line-height: 46px;
  }

  .banner__form__submit {
    margin-top: 55px;
    font-size: 1.07em;
  }

  .social-media__title {
    margin: 10px 0 25px;
  }

  .social-media__icons__list {
    margin-top: 25px;
  }

  .icons__link {
    width: 64px;
    height: 64px;
    line-height: 64px;
  }

  .tabs {
    padding-top: 50px;
  }

  .tabs__selector {
    width: 170px;
  }

  .tabs__description-block {
    padding: 30px 0 50px 80px;
  }

  .tabs__button {
    max-width: 230px;
    margin-top: 20px;
  }

  h2, .pricing__option__title {
    font-size: 1.71em;
  }

  .section__title, .section__title:after {
    margin-top: 45px;
  }

  .section__title {
    margin-bottom: 30px;
  }

  .section__title:after {
    width: 140px;
  }

  .section__description-text {
    margin-bottom: 30px;
  }

  .benefits__list-item {
    padding-top: 35px;
  }

  .benefits__icon-block {
    width: 80px;
    height: 80px;
    margin-bottom: 65px;
    line-height: 80px;
  }

  .benefits__icon {
    transform: scale(.85);
  }

  h4 {
    margin-bottom: 30px;
  }

  .pricing__options {
    margin-top: 50px;
  }

  .pricing__option__title {
    line-height: 52px;
  }

  .pricing__option__price {
    line-height: 88px;
  }

  .pricing__option__description-text, .pricing__option__item {
    line-height: 43px;
  }

  .reviews__author__message {
    height: 180px;
  }

  .reviews__author__avatar-img {
    width: 65px;
  }

  .reviews__author__name {
    margin: 10px 0 15px;
  }

  .reviews__author__post {
    margin-bottom: 10px;
  }

  .stylish__description-text {
    margin: 30px 0;
  }

  .stylish__button {
    max-width: 320px;
  }

  .contact-us__description-text {
    margin-bottom: 60px;
  }

  .contact-us__form__required, .contact-us__form__message {
    width: calc(50% - 10px);
  }

  .contact-us__form__submit {
    max-width: 320px;
  }

  .footer__icons__link {
    width: 45px;
    height: 45px;
    line-height: 45px;
  }
}

@media (max-width: 992px) {
  .header {
    padding-left: 20px;
    padding-right: 20px;
  }

  .header > .container {
    position: initial;
  }

  .header > .container:before, .banner > .container:before {
    content: none;
  }

  .header__logo-link {
    height: 55px;
    line-height: 55px;
  }

  .header__logo-img {
    max-height: 22px;
  }

  .header__menu-toggle {
    display: block;
  }

  .header__menu-block {
    display: initial;
    background: var(--secondary-background);
    width: calc(100% - 40px);
