.stylish {
    background: linear-gradient(to bottom, var(--half-opacity-black), var(--half-opacity-black)), url(../images/stylish.webp);
    background-position: center bottom;
    position: relative;    
    text-align: center;
}
.stylish__title,
.stylish__description-text {
    color: var(--secondary-text);
}
.stylish__title::after {
    border-color: var(--primary-background);
}
.stylish__description-text {
    margin-bottom: 75px;
}
.stylish__button {
    max-width: 370px;
}